@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 62.5%;
}

body {
  margin: 0;
  background-color: #f8961e;
}

.expenses {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 80rem;
  max-width: 95%;
}

.card {
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.15),
    -1rem -1rem 2rem rgba(255, 255, 255, 0.15);
  border-radius: 1rem;
}

.expenses-filter {
  color: white;
  padding: 0 1rem;
  font-size: 2rem;
  font-weight: bold;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

.no-items {
  color: #f9c74f;
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
}
.list {
  padding: 0;
}

.container {
  font-size: 1.5rem;
  margin: 2rem;
  background-color: #f8961e;
  padding: 1rem;
  color: aliceblue;
  color: #000;
  font-weight: bold;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.items {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container .date {
  font-size: 2rem;
}
.items .price {
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  background-color: #43aa8b;
  padding: 0.5rem;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.1) inset,
    -1rem -1rem 2rem rgba(255, 255, 255, 0.1) inset;
  border-radius: 0.5rem;
}

@media only screen and (min-width: 696px) {
  .items {
    flex-direction: row;
    flex: 1 1;
  }
  .items h2 {
    margin-right: 2rem;
    flex: 2 1;
  }
}

.date {
  list-style: none;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  background-color: #43aa8b;
  color: #fff;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.15) inset,
    -1rem -1rem 2rem rgba(255, 255, 255, 0.15) inset;
  width: 15rem;
}
.date-month,
.date-year {
  font-size: 2rem;
}

.date .date-day {
  font-size: 3.5rem;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar-inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #57759080;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar-fill {
  background-color: #277da1;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar-label {
  font-weight: bold;
  font-size: 1rem;
  text-align: center;
}

.chart {
  margin: 1.5rem 1rem;
  padding: 1rem;
  border-radius: 1rem;
  background-color: #f9c74f;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}

.form-container {
  width: 100%;
  font-size: 2.5rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.form-container label {
  font-weight: bold;
  display: block;
  text-align: left;
}
.form-element {
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
}
.form-container input {
  text-align: left;
  border-radius: 0.2rem;
  border: 0.05rem solid black;
  width: 20rem;
  max-width: 100%;
  padding: 0.5rem;
}
.form-container .btn {
  margin-top: 2rem;
  font-size: 1.6rem;
  padding: 1rem;
  border-radius: 0.5rem;
  border: none;
  width: 20rem;
  background-color: #f9c74f;
  font-weight: bold;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.1) inset,
    -1rem -1rem 2rem rgba(255, 255, 255, 0.1) inset;
}
.cancel-btn {
  margin-top: 2rem;
  margin-left: 1rem;
  font-size: 1.6rem;
  padding: 1rem;
  border-radius: 0.5rem;
  border: none;
  color: #fff;
  background-color: #f94144;
  font-weight: bold;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.1) inset,
    -1rem -1rem 2rem rgba(255, 255, 255, 0.1) inset;
}
.add-expense-btn {
  font-size: 1.6rem;
  padding: 1rem;
  border-radius: 0.5rem;
  border: none;
  width: 20rem;
  background-color: #f9c74f;
  font-weight: bold;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.1) inset,
    -1rem -1rem 2rem rgba(255, 255, 255, 0.1) inset;
}
.form-container .btn:hover,
.add-expense-btn,
.cancel-btn {
  cursor: pointer;
}
@media only screen and (min-width: 696px) {
  .form-element {
    flex-basis: 40%;
  }
  .form-container {
    justify-content: space-around;
  }
  .button-element {
    flex-direction: row;
  }
}

.new-expenses {
  background-color: #43aa8b;
  padding: 1rem;
  margin: 4rem auto;
  width: 90rem;
  max-width: 95%;
  text-align: center;
}

