.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar-inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #57759080;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar-fill {
  background-color: #277da1;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar-label {
  font-weight: bold;
  font-size: 1rem;
  text-align: center;
}
